.odp_star {
  padding: 0 7px;
  display: inline-block;
  width: 16px;
  position: relative;
  cursor: help;
  vertical-align: middle;

  &:before {
    font-family: Dashicons;
    font-weight: 400;
    font-size: 1.2em;
    content: "";
    color: #666;
  }
}

.form-table {
  th {
    position: relative;

    .odp_star {
      position: absolute;
      right: -10px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

/**
 * Product page
 */
#room_options h4 {
  margin: 0 10px 15px 10px;
}

#room_options .odp_star {
  font-size: 14px;
}

#wp-room_features-wrap {
  padding: 0 12px;
}

#wp-room_features-wrap .wp-editor-area {
  border: 1px solid #ddd;
}

.form_field_resources, .form_field_deposits {
  margin-bottom: 20px;
  background: #cccccc;
  padding: 20px 0 30px;
}

.room_resource .header, .room_deposit .header {
  display: flex;
  margin: 14px 0;
}

.room_resource .room_resource_inputs, .room_deposit .room_deposit_inputs {
  display: flex;
}

.room_resource .room_resource_inputs .header input, .room_deposit .room_deposit_inputs .header input {
  width: 100%;
}

.room_resource .header > div:first-child, .room_deposit .header > div:first-child {
  flex-grow: 1;
}

.room_resource .header > div, .room_deposit .header > div {
  padding: 0 10px;
}

.add_room_resource, .add_room_deposit {
  margin: 20px 10px 0 10px !important;
}